<template>
    <div class="wallpaper-pattern">
        <add-favorite v-bind:pattern="data"></add-favorite>
        <router-link :to="`/pdp/${data.design.name}`" :id="`pattern-link-${data.sku}`">
        <div class="color-corner-wrap">
            <div class="color-corner" :style="{'background-color': data.design.color}"></div>
        </div>
            <img v-bind:src="data.image">
            <h3>{{data.design.name}}</h3>
        </router-link>
    </div>
</template>

<script>
import AddFavorite from '@/components/favorites/AddFavorite'

    export default {
        props: [
            'data'
        ],
        components : {
            AddFavorite
        }
    }
</script>



