<template>
    <div class="filter-search">
        <div class="filter-container">
            <div class="filters" v-show="!mobileFiltersShown">

                <h2  class="filter-header">SORT
                    <span v-if="showSort" class="button plain search-page-expanded" @click="showSort = !showSort"></span>
                    <span v-if="!showSort" class="button plain search-page" @click="showSort = !showSort"></span>
                </h2>
                <div v-if="showSort">
                    <h5>
                        TODO : custom icons? MAKE THIS EXPANDED TO FIT CONTENT
                    </h5>
                    <h4>Sort by newest</h4>
                    <h4>Random</h4>

                    <p>todo: make this sticky</p>
                    <p>todo: make filters and sorts collapasable?</p>
                    <p>todo: applied filters need <3</p>
                    <p>todo: applied filters on mobile</p>
                </div>
                <h2  class="filter-header">color filter
                    <span v-if="showColorFilters" class="button plain search-page-expanded" @click="showColorFilters = !showColorFilters"></span>
                    <span v-if="!showColorFilters"  class="button plain search-page" @click="showColorFilters = !showColorFilters"></span>
                </h2>
                <transition name="fade">
                    <color-filter v-if="showColorFilters"></color-filter>
                </transition>
                <h2 class="filter-header">pattern filter
                    <span v-if="showPatternFilters" class="button plain search-page-expanded" @click="showPatternFilters = !showPatternFilters"></span>
                    <span v-if="!showPatternFilters"  class="button plain search-page" @click="showPatternFilters = !showPatternFilters"></span>
                </h2>
                <pattern-filter v-if="showPatternFilters"></pattern-filter>
            </div>
            <transition name="fade">
                <div class="overlay" v-if="mobileFiltersShown" @click="hideMobileFilters"></div>
            </transition>
            <transition name="slide-right">
                <div class="filters-mobile" v-show="mobileFiltersShown">
                    <div class="close" @click="hideMobileFilters">X (use icon from menu)</div>
                    <h2>color filter</h2>
                    <color-filter></color-filter>
                    <h2>pattern filter</h2>
                    <pattern-filter></pattern-filter>
                </div>
            </transition>

            <div class="results">
                <!-- <div v-for="item in algoliaResults">
                    {{item}}
                </div> -->
                <div class="button-wrap under-s">
                    <div class="plain button" id="mobile-filters-button" @click="showMobileFilters">Filters</div>
                </div>
                <filtered-patterns></filtered-patterns>
                <!--<patterns></patterns>-->
            </div>
        </div>
    </div>
</template>

<script>
    import {mapGetters, mapActions} from 'vuex'
    import Patterns from './patterns/AllPatterns'
    import ColorFilter from './filters/ColorFilter'
    import PatternFilter from './filters/PatternFilter'
    import AppliedFilters from './filters/AppliedFilters'
    import FilteredPatterns from './patterns/FilteredPatterns'

    export default {
        components: {
            Patterns, ColorFilter, PatternFilter, AppliedFilters, FilteredPatterns
        },
        name: 'HelloWorld',
        data () {
            return {
                msg: 'Welcome to Your Vue.js App',
                showColorFilters: false,
                showPatternFilters: false,
                showSort: false,
            }
        },
        computed: {
            ...mapGetters({
                mobileFiltersShown: 'getShowMobileFilters'
            })
        },
        methods: {
            ...mapActions({
                showMobileFilters: 'showMobileFilters',
                hideMobileFilters: 'hideMobileFilters',
                searchAlgolia: 'searchAlgolia'
            })
        },
        mounted() {
            this.searchAlgolia({"searchTerm": ''})
            window.scrollTo(0, 0)
        }
        /*  components : {
         TODO: Add components
         }*/
    }
</script>




